<template>
  <div>
    <header-component title="注册"></header-component>
    <router-view></router-view>
    <div class="am-box">
      <ul class="am-list am-two-reg">
        <li>
          <router-link :to="PhoneRegister">
            <a href="javascript:;">手机注册</a>
          </router-link>
        </li>
        <!--<li><a href="/visit/normalregister">普通注册</a></li>-->
      </ul>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };

  .am-box{
    width: 100%;
  }

  .am-two-reg{
    width: 80%;
    margin-top: n(150);
    margin-left: 10%;
  }
  .am-box .am-two-reg li{
    width: 100%;
    height: n(80);
    line-height: n(80);
    padding: 0;
    background: #fee837;
    margin-bottom: n(50);

  }

  .am-box .am-two-reg li a{
    width: 100%;
    font-size: n(30);
    text-align: center;
    display: block;
    padding: 0;
  }
</style>
<script>
  import HeaderComponent from './header.vue'
  var urlhost = process.env.API_AJAX;
  export default {
    name:"ActivityRegister",
    data() {
      return {

      }
    },
    props: {
      PhoneRegister: {
        type: Object,
        default () {
          return {
            name: 'ActivityPhone'
          }
        }
      },
    },
    components: {
      HeaderComponent,
    },
    methods:{

    }

  }
</script>



